<template>
 <div class="categories">
   <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/categories' }">categories</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/categories' }">list</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-col :span="12">
	    	<el-row>
	    		<el-tooltip class="item" effect="dark" content="Add new" placement="top">
			      <el-button type="primary" icon="el-icon-plus" circle @click="$router.push({path:'/categories/create'})"></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Export Selected" placement="top">
			      <el-button type="warning" icon="el-icon-download" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Publish Selected" placement="top">
			      <el-button type="success" icon="el-icon-check" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Draft Selected" placement="top">
			      <el-button type="info" icon="el-icon-close" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Archived Selected" placement="top">
			      <el-button type="danger" icon="el-icon-delete" circle></el-button>
			    </el-tooltip>
			</el-row>
    	</el-col>
        <el-col :span="12">
        	<el-row style="float: right;">
			  <el-button icon="el-icon-search" circle></el-button>
			</el-row>
        </el-col>
    	
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-table
		    ref="multipleTable"
		    :data="categories"
		    style="width: 100%"
		    @row-dblclick="dblClickRow"
		    @selection-change="handleSelectionChange">

		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>

		    <el-table-column
		      label="Category Name">
		      <template slot-scope="scope">{{ scope.row.category_name }}</template>
		    </el-table-column>

		    <el-table-column
		      property="category_description"
		      label="Description"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      property="category_status"
		      label="Status"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      property="date_created"
		      label="Created"
		      show-overflow-tooltip>
		    </el-table-column>

		  </el-table>
    </el-row>
    </div>
</template>
<style>
.categories {
	margin: -8px;
}
</style>

<script>
import { mapState, mapActions } from 'vuex'
export default {
	name : "Categories",
	props: {
		msg: String
    },
    computed: mapState({
	    categories: state => state.categories.all
	}),
    data() {
    	return {
    		multipleSelection: []
	    	}
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      dblClickRow(row, event, column) {
      	console.log('row ',row)
      	console.log('event ',event)
      	console.log('column ',column)
      }
    },
    created () {
	    this.$store.dispatch('categories/getAllCategories')
	}
}
</script>


