<template>
 <div class="stores">
   <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/stores' }">stores</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/stores' }">list</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-col :span="12">
	    	<el-row>
	    		<el-tooltip class="item" effect="dark" content="Add new" placement="top">
			      <el-button type="primary" icon="el-icon-plus" circle @click="$router.push({path:'/stores/create'})"></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Export Selected" placement="top">
			      <el-button type="warning" icon="el-icon-download" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Archived Selected" placement="top">
			      <el-button type="danger" icon="el-icon-delete" circle></el-button>
			    </el-tooltip>
			</el-row>
    	</el-col>
        <el-col :span="12">
        	<el-row style="float: right;">
			  <el-button icon="el-icon-search" circle></el-button>
			</el-row>
        </el-col>
    	
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-table
		    ref="multipleTable"
		    :data="tableData3"
		    style="width: 100%"
		    @row-dblclick="dblClickRow"
		    @selection-change="handleSelectionChange">

		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>

		    <el-table-column
		      label="Store Name"
		      show-overflow-tooltip>
		      <template slot-scope="scope">{{ scope.row.store_name }}</template>
		    </el-table-column>

		    <el-table-column
		      property="store_address"
		      label="Store Address"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      property="date_created"
		      label="Created"
		      show-overflow-tooltip>
		    </el-table-column>

		  </el-table>
    </el-row>
 </div>
</template>
<style>
.stores {
	margin: -8px;
}
</style>

<script>
export default {
	name : "Stores",
	props: {
		msg: String
    },
    data() {
    	return {
    		tableData3: [{
	          date_created: '2016-05-03',
	          store_name: 'Gaisano Mall',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-02',
	          store_name: 'Centrio Ayala',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-04',
	          store_name: 'Ayala Abreeza',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-01',
	          store_name: 'Ketkai Mall',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-08',
	          store_name: 'Super Mall',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-06',
	          store_name: 'Uptown Great Mall',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-07',
	          store_name: 'MAll Of Asia',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }, {
	          date_created: '2016-05-07',
	          store_name: 'Ororama',
	          store_address: 'lorem ipsum dolor set emit churvah'
	        }],
	        multipleSelection: []
	    	}
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      dblClickRow(row, event, column) {
      	console.log('row ',row)
      	console.log('event ',event)
      	console.log('column ',column)
      }

    }
}
</script>


