<template>
 <div class="categories_create">
    <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/categories' }">category list</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/categories/create' }">create</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
    
	<div style="margin: 10px 0;"></div>
    <el-row>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>Add Category</span>
        <el-button style="float:right; margin-top: -10px;"  type="primary" plain>Save</el-button>
      </div>
      <div class="text item">
        <el-col :span="24">
         <el-form ref="form" :model="form" label-width="120px">
              
              <el-form-item label="Name">
                <el-input v-model="form.name"></el-input>
              </el-form-item>

              <el-form-item label="Description">
                <el-input v-model="form.description"></el-input>
              </el-form-item>      
              

              <el-form-item label="Publish">
                <el-switch v-model="form.delivery"></el-switch>
              </el-form-item>

         </el-form>
        </el-col>
      </div>
    </el-card>
     
            
    </el-row>
 </div>
</template>
<style>
.categories_create {
	margin: -8px;
}
.el-breadcrumb {
    font-size: 11px;
    line-height: 1;
}
.clickable {
	cursor: pointer;
}
</style>

<script>
export default {
	name : "CategoriesCreate",
	props: {
		msg: String
    },
    data() {
    	return {
    		category_details: {
              date_created: '2016-05-03',
              category_name: 'Hardware',
              category_description: 'lorem ipsum dolor set emit churvah',
              category_status: 'active'
            },
            form: {
              name: '',
              description: ''
            },
            labelPosition: 'top',
	    }
    },
    methods: {
    	
    }
}
</script>


