<template>
 <div class="dashboard">
 <el-row>
 	<el-col :span="8">
 		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <span>Login</span>
		  </div>
		  <div>
		  <el-form :model="loginForm" status-icon>
		      <div style="margin: 10px 0;"></div>
			  <el-form-item label="Username" prop="username">
			    <el-input v-model.number="loginForm.username"></el-input>
			  </el-form-item>
		  	 <el-form-item label="Password" prop="password">
			    <el-input type="password" v-model="loginForm.password" auto-complete="off"></el-input>
			  </el-form-item>
			  <div style="margin: 10px 0;"></div><div style="margin: 10px 0;"></div>
			  <el-button @click="login" type="primary" plain style="width:100%">Primary</el-button>
		 </el-form>
		  </div>
		</el-card>
 	</el-col>
 </el-row>
 </div>
</template>
<style>
.dashboard {
	margin: -8px;
}
.el-form-item {
    margin-bottom: 3px;
}
</style>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

export default {
	name : "Dashboard",
	props: {
		msg: String
    },
    data () {
    	return {
    		loginForm: {
	          password: '',
	          username: '',
	        },
    	}
    },
    methods : {
    	login() {
    		console.log('pw ',this.loginForm.username)
    		console.log('pw ',this.loginForm.password)
    		// Vue.axios.post('http://172.20.10.3:8000/rest-auth/login/').then((response) => {
	     	//   console.log('response ',response)
	    	// })
			axios.post('http://localhost:8000/rest-auth/login/', {
		      body: this.loginForm
		    })
		    .then(response => {
		    	console.log('response ',response)
		    })
		    .catch(e => {
		      this.errors.push(e)
		      console.log('error ',e)
		    })
    	}
    }
}
</script>



