import producttypes from '../../api/producttypes'

// initial state
const state = {
  all: []
}

// getters
const getters = {}

// actions
const actions = {
  getAllProductTypes ({ commit }) {
    producttypes.getProductTypes(resp => {
      commit('setProductTypes', resp)
    })
  }
}

// mutations
const mutations = {
  setProductTypes (state, producttypes) {
    state.all = producttypes
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


