<template>
 <div class="products_create">
    <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/products' }">product list</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/products/create' }">create</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
    
	<div style="margin: 10px 0;"></div>

	<el-row>
	<el-card class="box-card">
	  <div slot="header" class="clearfix">
	    <span>Add Product</span>
	    <el-button style="float:right; margin-top: -10px;"  type="primary" plain>Save</el-button>
	  </div>
	  <div class="text item">
	  <el-form ref="form" :model="form" label-width="120px">
	  	<el-col :span="12">

			  
			  <el-form-item label="Is Variant">
			    <el-switch v-model="form.is_variant"></el-switch>
			  </el-form-item>	

			  <el-form-item label="Parent" v-if="form.is_variant">
			    <el-select clearable v-model="form.parent" placeholder="please select Parent Product" style="width: 100%;">
			      <el-option label="Product 000001" value="Product 000001"></el-option>
			      <el-option label="Product 000002" value="Product 000002"></el-option>
			    </el-select>
			  </el-form-item>

			  <el-form-item label="Category" v-if="(form.is_variant && form.parent) || (!form.is_variant)">
			    <el-select clearable v-model="form.category" placeholder="please select Category" style="width: 100%;">
			      <el-option label="Hardware" value="Hardware"></el-option>
			      <el-option label="Software two" value="Software"></el-option>
			    </el-select>
			  </el-form-item>

			  <el-form-item label="Product Type" v-if="(form.is_variant && form.parent) || (!form.is_variant)">
			    <el-select clearable v-model="form.product_type" placeholder="please select a Type" style="width: 100%;">
			      <el-option label="Glass" value="Glass"></el-option>
			      <el-option label="Wood" value="Wood"></el-option>
			      <el-option label="Tile" value="Tile"></el-option>
			    </el-select>
			  </el-form-item>

			  <el-form-item label="Name">
			    <el-input v-model="form.name" clearable></el-input>
			  </el-form-item>

			  <el-form-item label="Code">
			    <el-input v-model="form.name" clearable></el-input>
			  </el-form-item>

			  <el-form-item label="Purchase Price">
			    <el-input v-model="form.purchase_price" clearable></el-input>
			  </el-form-item>

			  <el-form-item label="Retail Price">
			    <el-input v-model="form.retail_price" clearable></el-input>
			  </el-form-item>

			  <el-form-item label="Stocks">
			    <el-input v-model="form.stock_record" clearable></el-input>
			  </el-form-item>

			  <el-form-item label="Description">
			    <el-input type="textarea" v-model="form.desc" clearable></el-input>
			  </el-form-item>
			  
			  

			  			  

			  <!--<el-form-item>
			    <el-button type="primary" @click="onSubmit">Create</el-button>
			    <el-button>Cancel</el-button>
			  </el-form-item>-->
		</el-col>

	    <el-col :span="12">
	    	
			<div>
			<el-form-item :label="x.name" v-if="form.is_variant && form.product_type" v-for="x in form.attributes">
				    <el-input clearable v-model="x.value">x.value</el-input>
			</el-form-item>
			</div>
	   		<!-- <el-form-item label="Attributes" v-if="form.is_variant && form.product_type">
			    <el-select
				    v-model="form.value11"
				    multiple
				    collapse-tags
				    style="margin-left: 20px;width: 95%;"
				    placeholder="Select">
				    <el-option
				      v-for="item in form.options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
			</el-form-item> -->
	    	<el-form-item label="Thumbnail">
		    	<el-upload
				  class="upload-demo"
				  drag
				  action="https://jsonplaceholder.typicode.com/posts/"
				  :on-preview="handlePreview"
				  :on-remove="handleRemove"
				  :file-list="form.fileList"
				  style="float: right;width: 100%;"
				  multiple>
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
				  <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
				</el-upload>
			</el-form-item>

			<el-form-item label="Publish">
			    <el-switch v-model="form.delivery"></el-switch>
			</el-form-item>
			
	    </el-col>
		 </el-form>
	  </div>
	</el-card>
	</el-row>
    
 </div>
</template>
<style>
.products_create {
	margin: -8px;
}
.el-breadcrumb {
    font-size: 11px;
    line-height: 1;
}
.clickable {
	cursor: pointer;
}
</style>

<script>
export default {
	name : "ProductsCreate",
	props: {
		msg: String
    },
    data() {
    	return {
    		product_details: {
	          date_created: '2016-05-03',
	          product_name: 'Product 00001',
	          product_code: 'CODE-00001',
	          product_status: 'active',
	          product_stock_on_hand: '0',
	          product_variants: '5'
	        },
	        form: {
	          category: '',
	          product_type: '',
	          name: '',
	          region: '',
	          date1: '',
	          date2: '',
	          delivery: false,
	          type: [],
	          resource: '',
	          desc: '',
	          is_variant: false,
	          parent: '',
	          attributes: [{
		          name: 'Size',
		          value: ''
		        }, {
		          name: 'Color',
		          value: ''
		        }, {
		          name: 'Texture',
		          value: ''
		        }, {
		          name: 'Specs',
		          value: ''
		        }],
		        value11: [],
	          fileList: [{"name": "food.jpeg", "url": "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"}],
	        },
	        labelPosition: 'top'
	    }
    },
    methods: {
    	onSubmit() {
        	console.log('submit!');
      	},
      	handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
    }
}
</script>


