import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueRouter from 'vue-router'
import App2 from './App2.vue'


import Login from './components/Login.vue'
import Dashboard from './components/Dashboard.vue'
import Products from './components/Products.vue'
import ProductTypes from './components/ProductTypes.vue'
import Categories from './components/Categories.vue'
import Attributes from './components/Attributes.vue'
import Stores from './components/Stores.vue'
import Partners from './components/Partners.vue'

import CreateProducts from './components/Products/Create.vue'
import CreateProductTypes from './components/ProductTypes/Create.vue'
import CreateCategories from './components/Categories/Create.vue'
import CreateAttributes from './components/Attributes/Create.vue'
import CreateStores from './components/Stores/Create.vue'
import CreatePartners from './components/Partners/Create.vue'

// import VueNVD3 from 'vue-nvd3'
//import App from './App.vue'

Vue.use(VueAxios, axios)
Vue.use(VueRouter)
Vue.use(ElementUI)

const routes = [
  { path: '/login', component: Login },
  { path: '/dashboard', component: Dashboard },

  { path: '/products', component: Products },
  { path: '/products/create', component: CreateProducts },

  { path: '/producttypes', component: ProductTypes },
  { path: '/producttypes/create', component: CreateProductTypes },

  { path: '/categories', component: Categories },
  { path: '/categories/create', component: CreateCategories },

  { path: '/attributes', component: Attributes },
  { path: '/attributes/create', component: CreateAttributes },

  { path: '/stores', component: Stores },
  { path: '/stores/create', component: CreateStores },

  { path: '/partners', component: Partners },
  { path: '/partners/create', component: CreatePartners },

]

const router = new VueRouter({
  routes // short for `routes: routes`
})

// new Vue({
//   el: '#app',
//   render: h => h(App)
// })

const app = new Vue({
  router,
  template: '<App2/>',
  render: h => h(App2),
  el: '#app'
}).$mount('#app')


