import categories from '../../api/categories'

// initial state
const state = {
  all: []
}

// getters
const getters = {}

// actions
const actions = {
  getAllCategories ({ commit }) {
    categories.getCategories(resp => {
      commit('setCategories', resp)
    })
  }
}

// mutations
const mutations = {
  setCategories (state, categories) {
    state.all = categories
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


