<template>
 <div class="producttypes_create">
    <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/producttypes' }">product type list</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/producttypes/create' }">create</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
    
	<div style="margin: 10px 0;"></div>
    <el-row>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>Add Product Type</span>
        <el-button style="float:right; margin-top: -10px;"  type="primary" plain>Save</el-button>
      </div>
      <div class="text item">
        <el-col :span="24">
         <el-form ref="form" :model="form" label-width="120px">
              
              <el-form-item label="Name">
                <el-input v-model="form.name"></el-input>
              </el-form-item>

              <el-form-item label="Code">
                <el-input v-model="form.description"></el-input>
              </el-form-item>      
              

              <el-form-item label="Publish">
                <el-switch v-model="form.delivery"></el-switch>
              </el-form-item>

              <el-form-item label="Attributes">
                <el-checkbox-group v-model="form.attributes">
                    <el-checkbox label="Size" border></el-checkbox>
                    <el-checkbox label="Color" border></el-checkbox>
                    <el-checkbox label="Texture" border></el-checkbox>
                    <el-checkbox label="Specs" border></el-checkbox>
                    <el-checkbox label="Tangibility" border></el-checkbox>
                    <el-checkbox label="Surface" border></el-checkbox>
                    <el-checkbox label="Glass" border></el-checkbox>
                    <el-checkbox label="Plastic" border></el-checkbox>
                    <el-checkbox label="Steel" border></el-checkbox>
                    <el-checkbox label="Wood" border></el-checkbox>
                    <el-checkbox label="Pastres" border></el-checkbox>
                </el-checkbox-group>
              </el-form-item>

         </el-form>
        </el-col>
      </div>
    </el-card>
     
            
    </el-row>
 </div>
</template>
<style>
.producttypes_create {
	margin: -8px;
}
.el-breadcrumb {
    font-size: 11px;
    line-height: 1;
}
.clickable {
	cursor: pointer;
}
</style>

<script>
export default {
	name : "ProductTypeCreate",
	props: {
		msg: String
    },
    data() {
    	return {
    		product_type_details: {
	          date_created: '2016-05-08',
	          name: 'Cement',
	          description: 'lorem ipsum dolor set emit churvah'
	        },
            form: {
              name: '',
              description: '',
              attributes: ['Option A']
            },
            labelPosition: 'top'
	    }
    },
    methods: {
    	
    }
}
</script>


