<template>
 <div class="products">
    <el-row>
     <el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/products' }">products</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/products' }">list</el-breadcrumb-item>
	</el-breadcrumb>
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-col :span="12">
	    	<el-row>
	    		<el-tooltip class="item" effect="dark" content="Add new" placement="top">
			      <el-button type="primary" icon="el-icon-plus" circle @click="$router.push({path:'/products/create'})"></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Export Selected" placement="top">
			      <el-button type="warning" icon="el-icon-download" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Publish Selected" placement="top">
			      <el-button type="success" icon="el-icon-check" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Draft Selected" placement="top">
			      <el-button type="info" icon="el-icon-close" circle></el-button>
			    </el-tooltip>
			    <el-tooltip class="item" effect="dark" content="Archived Selected" placement="top">
			      <el-button type="danger" icon="el-icon-delete" circle></el-button>
			    </el-tooltip>
			</el-row>
    	</el-col>
        <el-col :span="12">
        	<el-row style="float: right;">
			  <el-button icon="el-icon-search" circle></el-button>
			</el-row>
        </el-col>
    	
    </el-row>
<div style="margin: 10px 0;"></div>
    <el-row>
    	<el-table
		    ref="multipleTable"
		    :data="tableData3"
		    style="width: 100%"
		    @row-dblclick="dblClickRow"
		    @selection-change="handleSelectionChange">
		    	<el-table-column
			      type="selection"
			      width="55">
			    </el-table-column>

			    <el-table-column
			      cell-style="clickable"
			      cell-class-name="clickable"
			      label="Product Name"
			      show-overflow-tooltip>
			      <template slot-scope="scope">{{ scope.row.product_name }}</template>
			    </el-table-column>

			    <el-table-column
			      property="product_code"
			      show-overflow-tooltip
			      label="Code">
			    </el-table-column>

			    <el-table-column
			      property="product_status"
			      show-overflow-tooltip
			      label="Status">
			    </el-table-column>

			    <el-table-column
			      property="product_stock_on_hand"
			      label="Stocks">
			    </el-table-column>

			    <el-table-column
			      property="product_variants"
			      label="Variants">
			    </el-table-column>

			    <el-table-column
			      property="date_created"
			      label="Date created"
			      show-overflow-tooltip>
			    </el-table-column>

		  </el-table>
    </el-row>
 </div>
</template>
<style>
.products {
	margin: -8px;
}
.el-breadcrumb {
    font-size: 11px;
    line-height: 1;
}
.clickable {
	cursor: pointer;
}
</style>

<script>
export default {
	name : "Products",
	props: {
		msg: String
    },
    data() {
    	return {
    		tableData3: [{
	          date_created: '2016-05-03',
	          product_name: 'Product 00001',
	          product_code: 'CODE-00001',
	          product_status: 'Published',
	          product_stock_on_hand: '0',
	          product_variants: '5'
	        }, {
	          date_created: '2016-05-02',
	          product_name: 'Product 00002',
	          product_code: 'CODE-00002',
	          product_status: 'Published',
	          product_stock_on_hand: '122',
	          product_variants: '0'
	        }, {
	          date_created: '2016-05-04',
	          product_name: 'Product 00003',
	          product_code: 'CODE-00003',
	          product_status: 'Published',
	          product_stock_on_hand: '0',
	          product_variants: '7'
	        }, {
	          date_created: '2016-05-01',
	          product_name: 'Product 00004',
	          product_code: 'CODE-00004',
	          product_status: 'Published',
	          product_stock_on_hand: '735',
	          product_variants: '0'
	        }, {
	          date_created: '2016-05-08',
	          product_name: 'Product 00005',
	          product_code: 'CODE-00005',
	          product_status: 'Published',
	          product_stock_on_hand: '0',
	          product_variants: '5'
	        }, {
	          date_created: '2016-05-06',
	          product_name: 'Product 00007',
	          product_code: 'CODE-00006',
	          product_status: 'Published',
	          product_stock_on_hand: '245',
	          product_variants: '0'
	        }, {
	          date_created: '2016-05-07',
	          product_name: 'Product 00008',
	          product_code: 'CODE-00007',
	          product_status: 'Published',
	          product_stock_on_hand: '897',
	          product_variants: '0'
	        }],
	        multipleSelection: []
	    	}
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      dblClickRow(row, event, column) {
      	console.log('row ',row)
      	console.log('event ',event)
      	console.log('column ',column)
      }

    }
}
</script>


