import attributes from '../../api/attributes'

// initial state
const state = {
  all: []
}

// getters
const getters = {}

// actions
const actions = {
  getAllAttributes ({ commit }) {
    attributes.getAttributes(resp => {
      commit('setAttributes', resp)
    })
  }
}

// mutations
const mutations = {
  setAttributes (state, attributes) {
    state.all = attributes
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


