<template>
  <div id="app">
       <section class="el-container is-vertical">
       <header class="el-header" style="height: 60px;"> </header>
         <section class="el-container">
         <aside class="el-aside" style="width: 215px;" v-if="$route.path != '/login'">
          <el-menu default-active="1" class="el-menu-vertical-demo" style="height: 100vh !important;" @open="handleOpen" @close="handleClose" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
          <el-menu-item index="1" @click="$router.push({path:'/dashboard'})">
            <i class="el-icon-menu"></i>
            <span>Dashboard</span>
          </el-menu-item>
              <el-submenu index="2">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span>Products</span>
                  </template>

                  <el-menu-item index="2-1" @click="$router.push({path:'/products'})">
                    <i class="el-icon-menu"></i>
                    <span>Product List</span>
                  </el-menu-item>

                  <el-menu-item index="2-2" @click="$router.push({path:'/producttypes'})">
                    <i class="el-icon-menu"></i>
                    <span>Product Type</span>
                  </el-menu-item>
              </el-submenu>

          <el-menu-item index="3" @click="$router.push({path:'/categories'})">
          <i class="el-icon-document"></i>
          <span>Categories</span>
          </el-menu-item>

          <el-menu-item index="4" @click="$router.push({path:'/attributes'})">
          <i class="el-icon-setting"></i>
          <span>Attributes</span>

          </el-menu-item>
          <el-menu-item index="5" @click="$router.push({path:'/stores'})">
          <i class="el-icon-document"></i>
          <span>Stores</span>

          </el-menu-item>
          <el-menu-item index="6" @click="$router.push({path:'/partners'})">
          <i class="el-icon-setting"></i>
          <span>Partners</span>

          </el-menu-item>
        </el-menu>
      </aside>
         <main class="el-main">
            <router-view></router-view>
         </main>
         </section>
       </section>
  </div>
</template>
<script>
import axios from 'axios'
import VueAxios from 'vue-axios'
import store from './store'

import Products from './components/Products.vue'
import Dashboard from './components/Dashboard.vue'
import ProductTypes from './components/ProductTypes.vue'
import Categories from './components/Categories.vue'
import Attributes from './components/Attributes.vue'
import Stores from './components/Stores.vue'
import Partners from './components/Partners.vue'

import CreateProducts from './components/Products/Create.vue'
import CreateProductTypes from './components/ProductTypes/Create.vue'
import CreateCategories from './components/Categories/Create.vue'
import CreateAttributes from './components/Attributes/Create.vue'
import CreateStores from './components/Stores/Create.vue'

export default {
  name: 'app',
  store,
  components: {
    axios,
    VueAxios,
    Dashboard,
    Products,
    ProductTypes,
    Categories,
    Attributes,
    Stores,
    Partners,
    CreateProducts,
    CreateProductTypes,
    CreateCategories,
    CreateAttributes,
    CreateStores
  },
  data() {
    const item = {
      date: '2016-05-02',
      name: 'Tom',
      address: 'No. 189, Grove St, Los Angeles'
      };
      return {
      tableData: Array(20).fill(item)
      }
  },
  methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
}
</script>

<style>
#app {
  margin: -8px;
}
.el-header{
    position: fixed;
    width: 100%;
    z-index: 999;
    background: #da5961;
}
.el-aside{
  position: absolute;
  margin-top: 60px;
}
.el-main {
  height: 100%;
  margin-top: 70px;
  margin-left: 220px;
}
.el-submenu .el-menu-item {
    height: 50px;
    line-height: 50px;
    padding: 0 45px;
    padding-left: 45px;
}
</style>



